.content {
  margin: 40px auto 0;
  padding: 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.main {
  flex-basis: 68%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.main > * {
  flex-basis: 48%;
}

.main > *:first-of-type {
  flex-basis: 100%;
}

.sidebar {
  flex-basis: 28%;
  display: flex;
  flex-flow: column;
  align-items: center;
}
