.container {
  width: 95%;
  margin: auto;
}

.main-banner {
  width: 100%;
  height: 40vh;
  overflow: hidden;
}

.main-banner img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.slider-container {
  margin: 0 auto;
  padding: 0px 5%;
  padding-top: 0px;
  padding-right: 5%;
  padding-bottom: 0px;
  padding-left: 5%;
  background-image: url(https://malina.artstudioworks.net/wp-content/themes/malina/images/bg-pattern.png) !important;
  background-position: 0 0 !important;
  background-repeat: repeat !important;
}
